  url('../font/fontello.woff2?74223752') format('woff2'),
  url('../font/fontello.woff?74223752') format('woff'),
  url('../font/fontello.ttf?74223752') format('truetype'),
  url('../font/fontello.svg?74223752#fontello') format('svg');
  font-weight: normal;
